<template>
  <div>
    <el-form ref="form" :model="form" :rules="rules" label-width="140px">
      <el-row>
        <el-col :span="10">
          <el-form-item label="商品发布平台">
            <el-checkbox-group v-model="form.issue">
              <el-checkbox label="app" disabled>app</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-col>
        <el-col :span="10" :offset="2">
          <el-form-item label="商品来源">
            <el-radio-group v-model="form.source">
              <el-radio label="jd">京东</el-radio>
              <el-radio label="wy">网易</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="10">
          <el-form-item label="商品名称" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10" :offset="2">
          <el-form-item label="商品副标题">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="10">
          <el-form-item label="商品app类目">
            <el-input v-model="form.type" disabled></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10" :offset="2" prop="name">
          <el-form-item label="商品排序号">
            <el-input v-model="form.sort" type="number"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="商品详情图" prop="defaultMsg">
            <div class="editor-container">
              <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue"></UE>
            </div>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="16" class="insideimg-box">
          <el-form-item label="内页图">
            <el-upload action="https://jsonplaceholder.typicode.com/posts/" list-type="picture-card" 
              :multiple=true :file-list="insideimg"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove" :on-success="upsuccess">
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImg" alt="">
            </el-dialog>
          </el-form-item>
        </el-col>
        <el-col :span="6" class="insideimg-hint">
          图片尺寸:高度不固定，宽度为750
        </el-col>
      </el-row>
      <div class="modu-title"><span>售后保障信息</span></div>
      <el-row>
        <el-col :span="22">
          <el-form-item label="是否支持开发票" prop="invoice">
            <el-radio-group v-model="form.invoice">
              <el-radio label="1">是</el-radio>
              <el-radio label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="服务说明">
            <el-checkbox-group v-model="form.serve">
              <el-checkbox label="peisong">国内部分地区不配送</el-checkbox>
              <el-checkbox label="youhui">不支持使用优惠券</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-col>
      </el-row>
      <div class="modu-title"><span>其它信息</span></div>
      <el-row>
        <el-col :span="10">
          <el-form-item label="是否上架" prop="putaway">
            <el-radio-group v-model="form.putaway">
              <el-radio label="1">是</el-radio>
              <el-radio label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="10" :offset="2">
          <el-form-item label="销售数量">
            <el-input v-model="form.salenum" type="number"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
       <el-row>
        <el-col :span="10">
          <el-form-item label="此商品可见企业">
            <el-select v-model="form.visible">
              <el-option v-for="item in form.visibles"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="10" :offset="2">
          <el-form-item label="关键字">
            <el-input v-model="form.keyword"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
        <el-button @click="onBack">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import { coCommodityInfo , saveCoCommodity , tyCommodityInfo } from '../../../api/api';
  import UE from '../../../components/ue/ue.vue';
  export default {
    data() {
      return {
        form: {
          issue: ['app'],
          source: 'jd',
          name: '',
          title: '',
          type: '',
          sort: '',
          invoice:'0',
          serve:['peisong','youhui'],
          putaway:'1',
          salenum:'',
          visible:'all',
          visibles:[{'value':'all','label':'全部可见'},{'value':'portion','label':'部分可见'}],
          keyword:'',
        },
        defaultMsg:'ss',
        insideimg: [],
        dialogImg:'',
        dialogVisible:false,
        config: {
          initialFrameWidth: null,
          initialFrameHeight: 350
        },
        ue1: "ue1",
        rules: {
          name: [
            { required: true, message: '请输入商品名称', trigger: 'blur' },
          ],
          defaultMsg: [
            { required: true, message: '请输入商品详情图', trigger: 'blur' }
          ],
          insideimg: [
            { required: true, message: '请插入内页图', trigger: 'blur' }
          ],
        }
      }
    },
    components: {UE},
    methods: {
       getUEContent() {
        let content = this.$refs.ue.getUEContent(); // 调用子组件方法
        this.$notify({
          title: '获取成功，可在控制台查看！',
          message: content,
          type: 'success'
        });
        console.log(content)
      },
      getUEContentTxt() {
        let content = this.$refs.ue.getUEContentTxt(); // 调用子组件方法
        this.$notify({
          title: '获取成功，可在控制台查看！',
          message: content,
          type: 'success'
        });
        console.log(content)
      },
      handleRemove(file,fileList) {
        console.log(fileList);
        this.insideimg=fileList;
      },
      handlePictureCardPreview(file) {
        this.dialogImg = file.url;
        this.dialogVisible = true;
      },
      upsuccess(response, file, fileList) {
        this.insideimg=fileList;
      },
      onSubmit() {
        console.log(this.insideimg);
      },
      onBack() {
        this.$router.back(-1);
      }
    }
  }
</script>
<style>
  .insideimg-box .el-form-item__content{
    border:dotted 1px #d7d7d7;
    padding:10px;
  }
  .insideimg-hint{
    font-size:14px;
    padding-left:10px;
    line-height: 24px;
    color:#999;
    padding-top:20px;
  }
  .modu-title{
    width:100%;
    position:relative;
    height:50px;
    margin-bottom: 22px;
  }
  .modu-title span{
    position:absolute;
    display:inline-block;
    width:500px;
    height:50px;
    left:0;
    top:0;
    text-align: left;
    line-height: 50px;
    font-size:20px;
    color:black;
    font-weight: bold;
  }
</style>


